'use strict'; 

let util = require('../core/util'); 

/**
 * Calculate Arrival Date for a Shipping Method/Speed
 * @param {String} selectedShippingMethod 
 */
let shippingMethodToPromisedArrivalDate = function(selectedShippingMethod){
    var addDays = function(date, days) {
        var result = new Date(date);
        result.setDate(result.getDate() + days);
        return result;
    }
    var currDate = new Date();
    // For now calculation can be done as below until we have service in place to calculate arrival date based on shipping method.
    // a.	If Shipping method Sameday: Add 1 day in current date to set the field value
    // b.	If Shipping method Standard: Add 3 day in current date to set the field value
    // c.	If Shipping method Express+: Add 2 day in current date to set the field value
    // d.	If Shipping method Saver: Add 5 day in current date to set the field value
    if(selectedShippingMethod == 'Sameday'){
        currDate = addDays(currDate, 1);
    }
    else if(selectedShippingMethod == 'Standard'){
        currDate = addDays(currDate, 3);
    }
    else if(selectedShippingMethod == 'Express+'){
        currDate = addDays(currDate, 2);
    }
    else {
        currDate= addDays(currDate, 5);
    }
    return currDate.toISOString();
}

/**
 * cart Line Item request payload
 * @param {String} LANG 
 * @param {Object} lineItem 
 * @param {Object} variantInfo 
 * @param {Object} designerData 
 * @param {String} taxMode
 */
let prepareLineItemRequest = function(LANG, lineItem, variantInfo, designerData, taxMode) {
    var customFields =  {};

    // if request has customFields key use that one
    // This way we can support any custom field
    if (util.typeOf(lineItem.customFields, 'object')) {
        customFields = lineItem.customFields;
    }
    // Note:  lineItemNumber Must be defined otherwise Custom Line Items will not be created
    if (util.typeOf(lineItem.lineItemNumber, 'string')) {
        customFields.lineItemNumber = lineItem.lineItemNumber; 
    }else if(!customFields.lineItemNumber){
        // build own if not suplied
        customFields.lineItemNumber = util.generateLineItemNumber();
    }
    // Allowing defining merchandisingProductName custom field
    if (util.typeOf(lineItem.merchandisingProductName, 'string')) {
        customFields.merchandisingProductName = lineItem.merchandisingProductName; 
    }
    // userSelection = userSelection
    if (util.typeOf(lineItem.userSelection, 'object')) {
        // It should be set of strings
        customFields.userSelection = util.objectToSetOfStrings(lineItem.userSelection); 
    }; 
    
    if (util.typeOf(variantInfo, 'object') && variantInfo.id) {

        // variableAttributes = userSelection + inputTip.variable: true
        var variableAttributes = [];
        var mcpSku;
        var shipingMethod;
        for(var i = 0, len = variantInfo.attributes.length; i < len; i++){
            var attribute = variantInfo.attributes[i];

            if(attribute.name == 'mcpSKU'){
                mcpSku = attribute.value.key;
            }else if(attribute.name == 'Shipping_Speed' && attribute.class == 'order' && lineItem.userSelection[attribute.label[LANG]]){
                shipingMethod = lineItem.userSelection[attribute.label[LANG]];
            }

            if(lineItem.userSelection[attribute.label[LANG]]){
                // Use value from user selection
                variableAttributes.push(attribute.label[LANG]+':'+lineItem.userSelection[attribute.label[LANG]]);
            }else{
                // If not user selectable, then also send one value
                // for inputTip.variable: true
                if(attribute.variable){
                    if(attribute.value && attribute.value.length && attribute.value[0].key){
                        variableAttributes.push(attribute.label[LANG]+':'+attribute.value[0].key);
                    }else if(attribute.value && attribute.value.key){
                        variableAttributes.push(attribute.label[LANG]+':'+attribute.value.key);
                    }
                }
            }
        }

        customFields.variableAttributes = variableAttributes;
        if(mcpSku){
            // add MCP specific custom fields
            customFields.mcpSku = mcpSku; 
        }

        // If order packet has promisedArrivalDate then use it, else calculate it
        if (util.typeOf(lineItem.promisedArrivalDate, 'string')) {
            
            customFields.promisedArrivalDate = lineItem.promisedArrivalDate;
        }else if(shipingMethod){
            // Add promised Arrival Date if shipping Method Label is supplied
            customFields.promisedArrivalDate = shippingMethodToPromisedArrivalDate(shipingMethod); 
        }
        lineItem.variantId = variantInfo.id;
    
    }else{
        throw new Error('Invalid variantInfo'); 
    }
    
    // add MCP specific custom fields
    if (util.typeOf(lineItem.mcpProductName, 'string')) {
        customFields.mcpPrductName = lineItem.mcpProductName; 
    }

    // Add MCP Designer data if supplied
    if (util.typeOf(designerData, 'object')) {
        // It should be set of strings
        customFields.mcpDocumentsSource = util.objectToSetOfStrings(designerData); 
    }

    // Validate required data
    if(!lineItem.productId || !lineItem.variantId || !lineItem.quantity){
        throw new Error('Invalid lineItem info'); 
    }

    var lineItemRequest =  {
        "productId":lineItem.productId, 
        "variantId":lineItem.variantId, 
        "quantity":lineItem.quantity, 
        "custom": {
            "type": {
                "key":"lineItemCustomFields"
            }, 
            "fields":customFields
        }
    }; 

    // Add External price if exists
    if (util.typeOf(lineItem.externalTotalCentAmount, 'number')) {
        if(!lineItem.currencyCode){
            throw new Error('Invalid currencyCode'); 
        }
        lineItemRequest.externalTotalPrice =  {
            "price": {
                // per item price
                //@todo cTool does not support non-numeric centAmount, it might be zero for now
                "currencyCode":lineItem.currencyCode, 
                "centAmount":parseInt(lineItem.externalTotalCentAmount/lineItem.quantity)
            }, 
            "totalPrice": {
                // total price from external pricing
                //@todo cTool does not support non-numeric centAmount
                "currencyCode":lineItem.currencyCode, 
                "centAmount":parseInt(lineItem.externalTotalCentAmount)
            }

        }; 
    }

    // If external tax rate Draft is supplied, use it as it is.
    if(util.typeOf(lineItem.externalTaxRate, 'object')){
        if(!lineItem.externalTaxRate.name || !lineItem.externalTaxRate.country){
            throw new Error('Invalid externalTaxRate'); 
        }
        lineItemRequest.externalTaxRate = lineItem.externalTaxRate;
    }
    
    if(taxMode == 'ExternalAmount' && lineItemRequest.externalTaxRate && lineItemRequest.externalTotalPrice){
        lineItemRequest.externalTaxAmount = {
            "totalGross": {
                "currencyCode":lineItem.currencyCode, 
                "centAmount":parseInt(lineItemRequest.externalTotalPrice.totalPrice.centAmount + (lineItemRequest.externalTotalPrice.totalPrice.centAmount * lineItemRequest.externalTaxRate.amount))
            },
            "taxRate": lineItemRequest.externalTaxRate
        };

        //@todo: spatel: will be removed once we have support from CTool
        lineItemRequest.custom.fields.externalTaxRateDraft = JSON.stringify(lineItemRequest.externalTaxRate);
    }

    return lineItemRequest;
}

/**
 * 
 * @param {Array} customLineItems 
 * @param {String} lineItemId 
 * @param {String} currencyCode 
 * @param {String} LANG 
 * @param {Boolean} appendActionName
 * @param {String} taxMode
 */
let prepareCustomLineItemRequest = function(customLineItems, lineItemId, currencyCode, LANG, appendActionName, taxMode){
    if(!util.typeOf(customLineItems, 'array')){
        throw new Error('Invalid CustomLineItems request parameter'); 
    }
    return customLineItems.map(function(customLineItem) {
        if(!util.typeOf(customLineItem.name, 'string') || !util.typeOf(customLineItem.externalTotalCentAmount, 'number')){
            throw new Error('Invalid CustomLineItem name/externalTotalCentAmount parameter'); 
        }
        var customFields = {
            "lineItemReference": lineItemId
        };
        if(customLineItem.serviceName){
            customFields.serviceName = customLineItem.serviceName;
        }
        var action = {
            "slug": customLineItem.name + '_' + lineItemId,
            "name": {},
            "money": {
                "currencyCode": currencyCode,
                "centAmount": customLineItem.externalTotalCentAmount
            },
            "custom": {
                "type": {
                    "key": "customlineItemCustomFields"
                },
                "fields": customFields
            }
        };
        if(appendActionName){
            action.action = 'addCustomLineItem';
        }
        action.name[LANG] = customLineItem.name;
        
        // if request has quantity
        if(util.typeOf(customLineItem.quantity, 'number')){
            action.quantity = customLineItem.quantity;
        }

        // if request has externalTaxRate or taxCategory
        if(util.typeOf(customLineItem.externalTaxRate, 'object')){
            action.externalTaxRate = customLineItem.externalTaxRate;
        }
        else if(util.typeOf(customLineItem.taxCategory, 'object')){
            action.taxCategory = customLineItem.taxCategory;
        }else{
            // @todo: spatel: This might be a invalid request
        }

        if(taxMode == 'ExternalAmount' && action.externalTaxRate){
            action.externalTaxAmount = {
                "totalGross": {
                    "currencyCode":currencyCode, 
                    "centAmount":parseInt(action.money.centAmount + (action.money.centAmount * action.externalTaxRate.amount))
                },
                "taxRate": action.externalTaxRate
            };

            //@todo: spatel: will be removed once we have support from CTool
            action.custom.fields.externalTaxRateDraft = JSON.stringify(action.externalTaxRate);
        }
        return action;
    });
}


/**
 * Search All Custom Line Items by Line Item Id
 * @param {Object} cart 
 * @param {String} lineItemId 
 */
let getCustomLineItemsByItemId = function(cart, lineItemId){
    var items = [];
    // search for customLineItems for lineItemId
    if(util.typeOf(cart.customLineItems, 'array')){
        // for loop is faster than filter, so using it
        for(var i = 0, len = cart.customLineItems.length; i < len; i++){
            if(cart.customLineItems[i].custom && cart.customLineItems[i].custom.fields.lineItemReference == lineItemId){
                items.push(cart.customLineItems[i]);
            }
        }
    }
    return items;
}
 /**
 * get Calculated Effective Price of Line Items
 * 
    taxedPrice : {
        "currencyCode": "GBP",
        "totalGrossCentAmount": 5824,// Inc. VAT, will be `null` if tax is not applied to cart yet
        "totalNetCentAmount": 5600,// Without VAT
        "totalTaxCentAmount": 933   //  VAT amount
    }
 * @param {Object} cart 
 * @param {String} lineItemId 
 */
let calculateEffectivePrice = function(cart, lineItemId){
    // @todo: for Plateform Tax Mode need to find way to get TaxedTotal of Custom Line Item & Line Item
    var taxedPrice = {};
    if(util.typeOf(cart.lineItems, 'array') && cart.lineItems.length){
        var lineItem = util.searchFirstByKeyValue(cart.lineItems, 'id', lineItemId);
        if(lineItem.id){
            // line Item found
            if(lineItem.taxedPrice){
                taxedPrice = {
                    "currencyCode": lineItem.taxedPrice.totalNet.currencyCode,
                    "totalGrossCentAmount": lineItem.taxedPrice.totalGross.centAmount,
                    "totalNetCentAmount": lineItem.taxedPrice.totalNet.centAmount,
                }
            }else if(lineItem.totalPrice){
                taxedPrice = {
                    "currencyCode": lineItem.totalPrice.currencyCode,
                    "totalGrossCentAmount": null,
                    "totalNetCentAmount": lineItem.totalPrice.centAmount,
                }
            }

            // search custom line items
            var relatedCustomLineItems = getCustomLineItemsByItemId(cart, lineItemId);
            if(relatedCustomLineItems.length){
                for(var i = 0, len = relatedCustomLineItems.length; i < len; i++){
                    
                    if(relatedCustomLineItems[i].taxedPrice){
                        taxedPrice.totalGrossCentAmount = (taxedPrice.totalGrossCentAmount ? taxedPrice.totalGrossCentAmount : 0) + relatedCustomLineItems[i].taxedPrice.totalGross.centAmount;
                        taxedPrice.totalNetCentAmount = (taxedPrice.totalNetCentAmount ? taxedPrice.totalNetCentAmount : 0) + relatedCustomLineItems[i].taxedPrice.totalNet.centAmount;
                    }else if(relatedCustomLineItems[i].totalPrice){
                        taxedPrice.totalNetCentAmount = (taxedPrice.totalNetCentAmount ? taxedPrice.totalNetCentAmount : 0) + relatedCustomLineItems[i].totalPrice.centAmount;
                    }
                    
                }
            }
        }

    }
    if(taxedPrice.currencyCode && taxedPrice.totalNetCentAmount){
        taxedPrice.totalTaxCentAmount = util.typeOf(taxedPrice.totalGrossCentAmount, 'number') ? taxedPrice.totalGrossCentAmount - taxedPrice.totalNetCentAmount : null;
    }
    return taxedPrice;
}

/** 
 * Get processed cart response containing line item level and order level effective price
 * 
 * @param {Object} cart
 * 
*/
let getProcessedCart = function(cart){
    var totalGrossCentAmount = null, totalNetCentAmount = null, currencyCode = null, totalTaxCentAmount = null;
    for(var currentIndex = 0, len = cart.lineItems.length; currentIndex < len; currentIndex++){
        var effectivePrice = calculateEffectivePrice(cart, cart.lineItems[currentIndex].id);
        cart.lineItems[currentIndex]['effectivePrice'] = effectivePrice;

        //Update the order level effective price
        if(!currencyCode){
            //for first time
            currencyCode = effectivePrice.currencyCode;
        }
        totalGrossCentAmount = getUpdatedTotalPrice(totalGrossCentAmount, effectivePrice['totalGrossCentAmount']);
        totalNetCentAmount = getUpdatedTotalPrice(totalNetCentAmount, effectivePrice['totalNetCentAmount']);
    }

    // calculate Tax amount only when cart has items & tax applied
    if(currencyCode && totalGrossCentAmount){
        totalTaxCentAmount = util.typeOf(totalGrossCentAmount, 'number') ? totalGrossCentAmount - totalNetCentAmount : null;
    }

    // Update cart level info
    cart['effectivePrice'] = {
        "currencyCode": currencyCode,
        "totalGrossCentAmount": totalGrossCentAmount,
        "totalNetCentAmount": totalNetCentAmount,
        "totalTaxCentAmount": totalTaxCentAmount
    };

    return cart;
};

let getUpdatedTotalPrice = function(totalPrice, priceToAdd){
    if(totalPrice != null){
        if(priceToAdd == null){
            return totalPrice;
        } else {
            //If the existing total amount is null, but the amount to be added in non-null numeric value, return the price to be added
            return (totalPrice + priceToAdd);
        }
    } else {
        if(priceToAdd == null){
            return null;
        } else {
            //If the existing total amount is null, but the amount to be added in non-null numeric value, return the price to be added
            return priceToAdd;
        }
    }
};

/**
 * Search within states for Specific StateType
 * @param {Array} states 
 * @param {String} LANG 
 * @param {String} stateName 
 * @param {String} stateType 
 */
let getFilteredStateByType = function(states, LANG, stateName, stateType){
    var _state = {};
    if (!util.typeOf(states, 'array')) {
        return _state;
    }
    for(var i = 0, len = states.length; i < len; i++){
        if(states[i].type == stateType && states[i].name && states[i].name[LANG] == stateName){
            _state = states[i];
            break;
        }
    }
    return _state;
};

let searchItemByCustomFieldValue = function(items, customFieldName, customFieldValue){
    var _item = {};
    if (!util.typeOf(items, 'array')) {
        return _item;
    }
    for(var i = 0, len = items.length; i < len; i++){
        if(items[i].custom && items[i].custom.fields[customFieldName] && items[i].custom.fields[customFieldName] == customFieldValue){
            _item = items[i];
            break;
        }
    }
    return _item;
};

/**
 * Build set Tax Amount actions 
 * @param {Object} cart 
 */
let buildExternalTaxAmountRequests = (cart) => {
    let actions = [];
    if(!cart.id){
        return actions;
    }

    // prepare for lineItems
    for(let i = 0, len = cart.lineItems.length; i < len; i++){
        if(cart.lineItems[i].custom && cart.lineItems[i].custom.fields && cart.lineItems[i].custom.fields.externalTaxRateDraft && cart.lineItems[i].totalPrice){
            let externalTaxRate = null;
            try{
                externalTaxRate = JSON.parse(cart.lineItems[i].custom.fields.externalTaxRateDraft);
            }catch(e){}

            let externalTaxAmount = {
                "totalGross": {
                    "currencyCode":cart.lineItems[i].totalPrice.currencyCode, 
                    "centAmount":parseInt(cart.lineItems[i].totalPrice.centAmount + (cart.lineItems[i].totalPrice.centAmount * externalTaxRate.amount))
                },
                "taxRate": externalTaxRate
            };
            
            actions.push({
                "action": "setLineItemTaxAmount",
                "lineItemId": cart.lineItems[i].id,
                "externalTaxAmount": externalTaxAmount
            });
        }
    }

    // prepare for customLineItems
    for(let i = 0, len = cart.customLineItems.length; i < len; i++){
        if(cart.customLineItems[i].custom && cart.customLineItems[i].custom.fields && cart.customLineItems[i].custom.fields.externalTaxRateDraft && cart.customLineItems[i].totalPrice){
            let externalTaxRate = null;
            try{
                externalTaxRate = JSON.parse(cart.customLineItems[i].custom.fields.externalTaxRateDraft);
            }catch(e){}

            let externalTaxAmount = {
                "totalGross": {
                    "currencyCode":cart.customLineItems[i].totalPrice.currencyCode, 
                    "centAmount":parseInt(cart.customLineItems[i].totalPrice.centAmount + (cart.customLineItems[i].totalPrice.centAmount * externalTaxRate.amount))
                },
                "taxRate": externalTaxRate
            };
            
            actions.push({
                "action": "setCustomLineItemTaxAmount",
                "customLineItemId": cart.customLineItems[i].id,
                "externalTaxAmount": externalTaxAmount
            });
        }
    }

    return actions;
};
module.exports = {
    prepareLineItemRequest: prepareLineItemRequest,
    prepareCustomLineItemRequest: prepareCustomLineItemRequest,
    getCustomLineItemsByItemId: getCustomLineItemsByItemId,
    calculateEffectivePrice: calculateEffectivePrice,
    getProcessedCart: getProcessedCart,
    getFilteredStateByType: getFilteredStateByType,
    searchItemByCustomFieldValue: searchItemByCustomFieldValue,
    buildExternalTaxAmountRequests,
}


