'use strict'; 

let util = require('../core/util'); 
let CartUtility = require('./CartUtility'); 
var parallel = require("async/parallel");

/**
 * @class OrderService
 */
module.exports = class OrderService {

    constructor(param) {
        const init = util.buildInitParam(param); 
        
        this.LANG = init.lang;
        this.EVENT_CART_CREATED = 'cart:created';
        this.EVENT_CART_FETCHED = 'cart:fetched';
        this.EVENT_CART_UPDATED = 'cart:updated';

        this.EVENT_ORDER_CREATED = 'order:created';
        this.EVENT_ORDER_FETCHED = 'order:fetched';
        this.EVENT_ORDER_UPDATED = 'order:updated';

        this._EventBus = init._EventBus;

        this._cartsUrl = function(cartId){
            return 'carts' + (cartId ? '/' + cartId : '');
        }
        
        this._ordersUrl = function(orderId){
            return 'orders' + (orderId ? '/' + orderId : '');
        }
        
        this._shippingMethodsUrl = function(id){
            return 'shipping-methods' + (id ? '/' + id : '');
        }
        
        this._statesUrl = function(id){
            return 'states' + (id ? '/' + id : '');
        }

    }

    /**
     * get cart by user Id, supports anonymous & logged in customer Id as well.
     * @param {String} userId 
     * @param {Boolean} isLoggedIn 
     */
    getCart(userId, isLoggedIn){
        if(isLoggedIn){
            // if logged in then fetch cart by customerId
            return util.makeCTGetRequest(this._cartsUrl(), {
                customerId: userId
            }).then(this._dispatchCartIsFetched.bind(this)); 
        }else{
            // if not logged in then fetch all carts by anonymousId
            // where, limit, offset, sort
            return util.makeCTGetRequest(this._cartsUrl(), {
                where: 'anonymousId="'+userId+'" and cartState="Active"',
                limit: 1,
                offset: 0,
                sort: 'lastModifiedAt desc'
            }).then(function(response){
                if(response.results){
                    // query was correct
                    // send only first cart result if exists
                    return (response.results.length ? response.results[0] : {statusCode:404});
                }
                // Other issue, pass as it is
                return response;
            }).then(this._dispatchCartIsFetched.bind(this)); 
        }
    }

    /**
     * Dispatch Cart Created Event
     * @param {Object} res 
     */
    _dispatchCartIsCreated(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_CART_CREATED, res);
        }
        return res;
    }

    /**
     * Dispatch Cart Fetched Event
     * @param {Object} res 
     */
    _dispatchCartIsFetched(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_CART_FETCHED, res);
        }
        return res;
    }

    /**
     * Dispatch Cart Updated Event
     * @param {Object} res 
     */
    _dispatchCartIsUpdated(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_CART_UPDATED, res);
        }
        return res;
    }

    /**
     * Get cart by cart Id
     * 
     * @param {String} cartId
     * 
    */
    getCartById(cartId){
        return util.makeCTGetRequest(this._cartsUrl(cartId)).then(this._dispatchCartIsFetched.bind(this));
    }

    /**
     * create cart
     * 
     * @param {Object} lineItem 
     * @param {String} userId 
     * @param {Boolean} isLoggedIn 
     * @param {Object} variantInfo 
     * @param {Object} designerData 
     * @param {String} taxMode
     *  External / Platform / Disabled
     */
    createCart(lineItem, userId, isLoggedIn, variantInfo, designerData, taxMode) {
        var _this = this;
        var lineItemRequest = {};
        // Build lineItem Request 
        try{
            lineItemRequest = CartUtility.prepareLineItemRequest(this.LANG, lineItem, variantInfo, designerData, taxMode);
        }catch(e){
            return util.sendValidationError(e);
        }
        var newCart =  {
            currency:lineItem.currencyCode, 
            lineItems:[ lineItemRequest ],
            "custom": {
                "type": {
                    "key":"orderCustomFields"
                },
                "fields": {}
            }
        }; 
        // If user is logged in, then use customerId key instead of anonymousId
        if (isLoggedIn) {
            newCart.customerId = userId; 
        }else {
            newCart.anonymousId = userId; 
        }
        if(util.typeOf(taxMode, 'string')){
            newCart.taxMode = taxMode; 
        }
        return util.makeCTPostRequest(this._cartsUrl(), newCart).then(this._dispatchCartIsCreated.bind(this)).then(function(_cart){
            if(_cart.id){
                return _this.reCalculateCart(_cart).then(function(_c){
                    return _this._addCustomLineItems(_c, lineItem.customLineItems, lineItem.currencyCode, lineItemRequest, taxMode);
                });
            }
            return _cart;
        });
    }

    /**
     * Add Item to existing cart
     * @param {Object} cartMeta 
     * @param {Object} lineItem 
     * @param {Object} variantInfo 
     * @param {Object} designerData 
     * @param {String} taxMode
     */
    addItemInCart(cartMeta, lineItem, variantInfo, designerData, taxMode){
        var _this = this;
        var lineItemRequest = {};
        // Build lineItem Request 
        try{
            lineItemRequest = CartUtility.prepareLineItemRequest(this.LANG, lineItem, variantInfo, designerData, taxMode);
            lineItemRequest.action = "addLineItem";
        }catch(e){
            return util.sendValidationError(e);
        }
        // add line Item request payload
        return this.updateCart(cartMeta, [ 
            lineItemRequest 
        ]).then(function(_cart){
            return _this._addCustomLineItems(_cart, lineItem.customLineItems, lineItem.currencyCode, lineItemRequest, taxMode);
        });
    }

    /**
     * Do Recalculate action on cart
     * Prices and tax rates will be updated.
     * This should be called just after create cart call
     * @todo: spatel: Need to reverify that `Do we need this call anymore?`
     * @param {Object} cartMeta 
     */
    reCalculateCart(cartMeta){
        return this.updateCart(cartMeta, [{
            "action":"recalculate", 
            "updateProductData":false
        }]);
    }

    /**
     * Wrapper for addCustomLineItems
     * @private
     * @param {Object} _cart 
     * @param {Array} customLineItems 
     * @param {String} currencyCode 
     * @param {Object} lineItemRequest 
     * @param {String} taxMode
     */
    _addCustomLineItems(_cart, customLineItems, currencyCode, lineItemRequest, taxMode){
        var _this = this;
        if(_cart.id && customLineItems && lineItemRequest.custom && lineItemRequest.custom.fields.lineItemNumber){
            // Search for line Item last added using lineItemNumber
            var lineItemObject = CartUtility.searchItemByCustomFieldValue(_cart.lineItems, 'lineItemNumber', lineItemRequest.custom.fields.lineItemNumber);
            if(lineItemObject.id){
                return _this.addCustomLineItems(_cart, lineItemObject.id, currencyCode, customLineItems, taxMode);
            }
        }
        return _cart;
    }

    /**
     * Add Custom Line Item for a line Item
     * @param {Object} cartMeta 
     * @param {String} lineItemId 
     * @param {String} currencyCode 
     * @param {*} customLineItems 
     * @param {String} taxMode
     */
    addCustomLineItems(cartMeta, lineItemId, currencyCode, customLineItems, taxMode){
        var customLineItemActions = {};
        // Build customLineItemActions Request 
        try{
            customLineItemActions = CartUtility.prepareCustomLineItemRequest(customLineItems, lineItemId, currencyCode, this.LANG, true, taxMode);
        }catch(e){
            return util.sendValidationError(e);
        }
        // send add custom line Item request payload
        return this.updateCart(cartMeta, customLineItemActions);
    }

    /**
     * Update existing Cart Info
     * @param {Object} cartMeta 
     * @param {Array} actions 
     */
    updateCart(cartMeta, actions){
        if(!util.typeOf(cartMeta, 'object') || !util.typeOf(actions, 'array') || !cartMeta.id || !cartMeta.version){
            return util.sendValidationError(new Error('Invalid update request parameter'));
        }
        var request = {
            "version":cartMeta.version, 
            "actions":actions
        };
        let _this = this;
        return util.makeCTPostRequest(this._cartsUrl(cartMeta.id), request).then(cart => {
            if(cart.id && cart.taxMode == 'ExternalAmount'){
                return _this.updateExternalTaxAmounts(cart);
            }
            return cart;
        }).then(this._dispatchCartIsUpdated.bind(this));
    }

    /**
     * Update External Tax Amounts
     * @param {Object} cart 
     */
    updateExternalTaxAmounts(cart){
        let actions = CartUtility.buildExternalTaxAmountRequests(cart);
        if(actions.length){
            return util.makeCTPostRequest(this._cartsUrl(cart.id), {
                "version":cart.version, 
                "actions":actions
            });
        }
        return cart;
    }

    /**
     * Update Line Item Custom Fields
     * @param {Object} cartMeta 
     * @param {String} lineItemId 
     * @param {Object} customFields 
            Custom field will be removed if value is `null`
            Note: a custom field can't be removed if it is not present in cart
            Do not pass key if you don't want to alter value
            So each parameter here in customFields are optional
     */
    updateCartItemCustomFields(cartMeta, lineItemId, customFields){
        if(!util.typeOf(customFields, 'object')){
            return util.sendValidationError(new Error('Invalid customFields request parameter'));
        }
        var actions = Object.keys(customFields).map(function(name) {

            // process field which should be set of string
            if( (name == 'userSelection'  || name == 'mcpDocumentsSource') && util.typeOf(customFields[name], 'object')){
                customFields[name] = util.objectToSetOfStrings(customFields[name]);
            }

            return {
                "action": "setLineItemCustomField",
                "lineItemId": lineItemId,
                "name": name,
                "value": customFields[name]
            }
        });
        return this.updateCart(cartMeta, actions);
    }

    /**
     * Remove Item from cart by Id
     * @param {Object} cart
     * @param {String} lineItemId 
     */
    removeCartItem(cart, lineItemId){
        var actions = [];
        // search for customLineItems for lineItemId
        var relatedCustomLineItems = CartUtility.getCustomLineItemsByItemId(cart, lineItemId);
        if(relatedCustomLineItems.length){
            for(var i = 0, len = relatedCustomLineItems.length; i < len; i++){
                actions.push({
                    action: "removeCustomLineItem",
                    customLineItemId : relatedCustomLineItems[i].id
                });
            }
        }
        actions.push({
            "action":"removeLineItem", 
            "lineItemId":lineItemId
        });
        return this.updateCart(cart, actions);
    }

    /**
     * Create order from cart
     * orderNumber: String that uniquely identifies an order. 
     * It can be used to create more human-readable (in contrast to ID) identifier for the order. It should be unique across a project. Once it’s set it cannot be changed.
     * @param {Object} cartMeta 
     * @param {Object} customFields
     *  {
            deviceInfo : Abc.order.objectToSetOfStrings(deviceInfo),
            deviceIPAddress : "127.0.0.1"
        }
     * @param {String} paymentState Paid
     * @param {String} orderState Confirmed
     * @param {String} orderTransitionState Confirmed
     * @param {String} orderNumber 
     */
    createOrder(cartMeta, customFields, paymentState, orderState, orderTransitionState, orderNumber){
        var _this = this;
        return new Promise(function (resolve, reject) {
            parallel({
                order: function(callback) {
                    _this.getCartById(cartMeta.id).then(function(_cart){
                        if(!_cart.id){
                            return callback(null, _cart);
                        }
                        var request = {
                            'id': _cart.id,
                            'version': _cart.version,
                            'orderNumber': orderNumber ? orderNumber : util.generateOrderNumber(),
                            'paymentState': paymentState ? paymentState : 'Pending',
                        };
                        util.makeCTPostRequest(_this._ordersUrl(), request).then(_this._dispatchOrderIsCreated.bind(_this)).then(function(_order){
                            callback(null, _order);
                        });
                    });

                },
                states: function(callback) {
                    _this.getStates().then(function(_states){
                        callback(null, _states);
                    });
                }
            }, function(err, results) {
                // Both calls ended
                if(err){
                    // for error also we do not need reject
                    return resolve(err);
                }

                if(!results.order || !results.order.id){
                    // order creation failed
                    return resolve(results.order);
                }

                // Do line Item state transitions & Order updates
                var actions = [];
                if(!results.order.custom || !results.order.custom.type){
                    // order custom fields are not enabled yet
                    actions.push({
                        "action": "setCustomType",
                        "type": {
                            "key": "orderCustomFields"
                        }
                    });
                }

                if(util.typeOf(orderState, 'string')){
                    // udate order state to Confirmed / Open
                    actions.push({
                        "action": "changeOrderState",
                        "orderState": orderState
                    });
                }

                // Update customFields if supplied
                if(util.typeOf(customFields, 'object') && Object.keys(customFields).length){
                    // if custom fields exists in customFields, Loop them and create action
                    for (var i = 0, keys = Object.keys(customFields), len = keys.length; i < len; i++) {
                        actions.push({
                            "action": "setCustomField",
                            "name": keys[i],
                            "value": customFields[keys[i]]
                        });
                    }
                }

                // Add actions to Update LineItemState
                if(util.typeOf(results.states.results, 'array')){

                    if(util.typeOf(orderTransitionState, 'string')){
                        // udate order transition state to Confirmed
                        var orderTransitionStateDetails = CartUtility.getFilteredStateByType(results.states.results,_this.LANG, orderTransitionState, 'OrderState');
                        actions.push({
                            "action": "transitionState",
                            "state": {
                                "typeId": "state",
                                "id": orderTransitionStateDetails.id
                            }
                        });
                    }
                    
                    var lineItemConfirmState = CartUtility.getFilteredStateByType(results.states.results,_this.LANG,'Confirmed', 'LineItemState');
                    if(lineItemConfirmState.id){

                        var lineItemDocUploadState = CartUtility.getFilteredStateByType(results.states.results,_this.LANG,'Waiting For User Upload', 'LineItemState');
                        var lineItemReviewState = CartUtility.getFilteredStateByType(results.states.results,_this.LANG,'Waiting For Doc Review', 'LineItemState');

                        // Add action to update all LineItem state in `Confirmed`
                        for(var i = 0, len = results.order.lineItems.length; i < len; i++){
                            var item = results.order.lineItems[i];
                            // update line item state to 'confirmed'
                            if(item.state){

                                actions.push({
                                    "action": "transitionLineItemState",
                                    "lineItemId": item.id,
                                    "quantity":item.quantity,
                                    "fromState": item.state[0].state,
                                    "toState": {
                                        "typeId": "state",
                                        "id": lineItemConfirmState.id
                                    }
                                });

                                // at same time update line item state to 'Waiting For User Upload'
                                if(lineItemDocUploadState.id){
                                    actions.push({
                                        "action": "transitionLineItemState",
                                        "lineItemId": item.id,
                                        "quantity":item.quantity,
                                        "fromState": {
                                            "typeId": "state",
                                            "id": lineItemConfirmState.id
                                        },
                                        "toState": {
                                            "typeId": "state",
                                            "id": lineItemDocUploadState.id
                                        }
                                    });

                                    // If Line Item has mcpDocument Details then update state to 'Waiting For Doc Review'
                                    if(item.custom && item.custom.fields.mcpDocumentsSource && item.custom.fields.mcpDocumentsSource.length && lineItemReviewState.id){
                                        actions.push({
                                            "action": "transitionLineItemState",
                                            "lineItemId": item.id,
                                            "quantity":item.quantity,
                                            "fromState": {
                                                "typeId": "state",
                                                "id": lineItemDocUploadState.id
                                            },
                                            "toState": {
                                                "typeId": "state",
                                                "id": lineItemReviewState.id
                                            }
                                        });
                                    }
                                }
                            }

                        }
                    }
                }

                return _this.updateOrder(results.order, actions).then(function(_order){
                    // resolve once order is updated
                    resolve(_order);
                });
            });
        });
    }

    /**
     * Dispatch Order Created Event
     * @param {Object} res 
     */
    _dispatchOrderIsCreated(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_ORDER_CREATED, res);
        }
        return res;
    }

    /**
     * Dispatch Order Updated Event
     * @param {Object} res 
     */
    _dispatchOrderIsUpdated(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_ORDER_UPDATED, res);
        }
        return res;
    }

    /**
     * Dispatch Order Fetched Event
     * @param {Object} res 
     */
    _dispatchOrderIsFetched(res){
        if(res.id){
            this._EventBus.dispatch(this.EVENT_ORDER_FETCHED, res);
        }
        return res;
    }

    /**
     * Update Order info
     * @param {Object} orderMeta 
     * @param {Array} actions 
     */
    updateOrder(orderMeta, actions){
         var request = {
            "version":orderMeta.version, 
            "actions":actions
        };
        return util.makeCTPostRequest(this._ordersUrl(orderMeta.id), request).then(this._dispatchOrderIsUpdated.bind(this));
    }

    /**
     * Update order items with Upload info
     * @param {Object} orderMeta 
     * @param {String} lineItemId 
     * @param {Array} filesUploaded 
     */
    updateOrderItemUploads(orderMeta, lineItemId, filesUploaded){
        var uploadsList = [];
        for(var i in filesUploaded){
            uploadsList.push(filesUploaded[i].handle+'::'+filesUploaded[i].filename+'::'+filesUploaded[i].url+'::'+(new Date()).toISOString());
        }

        return this.updateOrder(orderMeta, [{
            "action": "setLineItemCustomField",
            "lineItemId": lineItemId,
            "name": "userUploadsUrl",
            "value": uploadsList
        }]);
    }

    /** 
     * Marks userUploadComplete custom field as TRUE.
     * Transits line item state as WaitingForDocReview
     * 
     * @param {Object} orderMeta
     * @param {Object} lineItem
     * 
     * **/ 
    updateLineItemUserUploadStatus(orderMeta, lineItem){
        //Get line item states
        var _this = this;
        return this.getStates().then(function(states){
            if(states.results){
                var actions = [];
                var lineItemReviewState = CartUtility.getFilteredStateByType(states.results,_this.LANG,'Waiting For Doc Review', 'LineItemState');
                //Add actions for updating the line item custom field for User uploads and change state of the line item state
                if(lineItemReviewState.id){
                    // Line Item State found
                    actions.push({
                        "action": "setLineItemCustomField",
                        "lineItemId": lineItem.id,
                        "name": "userUploadComplete",
                        "value": true
                    });

                    actions.push({
                        "action": "transitionLineItemState",
                        "lineItemId": lineItem.id,
                        "quantity":lineItem.quantity,
                        "fromState": lineItem.state[0].state,
                        "toState": {
                            "typeId": "state",
                            "id": lineItemReviewState.id
                        }
                    });
                    return _this.updateOrder(orderMeta, actions);
                }

            }
            return states;
        });
        
    } 

    /**
     * get order by id
     * @param {String} orderId 
     */
    getOrderById(orderId) {
        return util.makeCTGetRequest(this._ordersUrl(orderId)).then(this._dispatchOrderIsFetched.bind(this)); 
    }

    /**
     * get Customer's orders
     * @param {String} customerId 
     * @param {Number} limit 
     * @param {Number} offset 
     * @param {String} sort 
     */
    getCustomerOrders(customerId, limit, offset, sort){
        return util.makeCTGetRequest(this._ordersUrl(), {
            where: 'customerId="'+customerId+'"',
            limit: limit ? limit : 50,
            offset: offset ? offset : 0,
            sort: sort ? sort : 'createdAt desc'
        });
    }

    /**
     * get Shipping Methods
     * @param {Number} limit 
     * @param {Number} offset 
     */
    getShippingMethods(limit, offset){
        return util.makeCTGetRequest(this._shippingMethodsUrl(), {
            limit: limit ? limit : 50,
            offset: offset ? offset : 0,
        }); 
    }

    /**
     * get States
     * @param {Number} limit 
     * @param {Number} offset 
     */
    getStates(limit, offset){
        return util.makeCTGetRequest(this._statesUrl(), {
            limit: limit ? limit : 50,
            offset: offset ? offset : 0,
        }); 
    }

    /**
     * Exposing Object to Set Of Strings Util function
     * @param {Object} obj 
     */
    objectToSetOfStrings(obj){
        return util.objectToSetOfStrings(obj);
    }
    
    /**
     * Exposing Set of Strings to Object Util function
     * @param {Array} stringSet 
     */
    setOfStringsToObject(stringSet){
        return util.setOfStringsToObject(stringSet);
    }

    /**
     * Search All Custom Line Items by Line Item Id
     * @param {Object} cart 
     * @param {String} lineItemId 
     */
    getCustomLineItemsByItemId(cart, lineItemId){
        return CartUtility.getCustomLineItemsByItemId(cart, lineItemId);
    }

    /**
     * get Calculated Effective Price of Line Items
     * 
        taxedPrice : {
            "currencyCode": "GBP",
            "totalGrossCentAmount": 5824,// Inc. VAT, will be `null` if tax is not applied to cart yet
            "totalNetCentAmount": 5600,// Without VAT
            "totalTaxCentAmount": 933   //  VAT amount
        }
        effectivePrice

     * 
     * @param {Object} cart 
     * @param {String} lineItemId 
     */
    getItemEffectivePrice(cart, lineItemId){
        return CartUtility.calculateEffectivePrice(cart, lineItemId);
    }

    /**
     * Get updated Cart object containing effective price at Line Item and Order level in the key 'effectivePrice'        
     * 
     * @param {Object} cart 
     * 
     */
    getProcessedCart(cart){
        return CartUtility.getProcessedCart(cart);
    }
}


